body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-heebo: Heebo;
  --font-open-sans: "Open Sans";

  /* font sizes */
  --font-size-base: 16px;
  --font-size-sm: 14px;
  --font-size-smi: 13px;
  --font-size-xs: 12px;
  --font-size-7xl: 26px;
  --font-size-5xl: 24px;
  --font-size-lg: 18px;
  --font-size-3xl: 22px;
  --font-size-21xl: 40px;
  --font-size-11xl: 30px;
  --font-size-xl: 20px;

  /* Colors */
  --color-white: #fff;
  --color-darkslategray-100: #4a4a4d;
  --color-darkslategray-200: #3c3c3c;
  --color-darkslategray-300: #2e2e2e;
  --color-sienna: #8b5d33;
  --color-gray-100: #8f8f93;
  --color-gray-200: #8c8c8c;
  --color-gray-300: #29292a;
  --color-gray-400: rgba(255, 255, 255, 0);
  --color-gray-500: rgba(255, 255, 255, 0.5);
  --color-whitesmoke: #f2f2f2;
  --color-dimgray-100: #727272;
  --color-dimgray-200: #606060;
  --color-black: #000;
  --color-gainsboro: #ddd;
  --color-khaki: #f9db76;
  --color-darksalmon: #be8a5e;

  /* Gaps */
  --gap-12xs: 1px;

  /* Paddings */
  --padding-xs-5: 11.5px;
  --padding-3xs: 10px;
  --padding-11xs: 2px;
  --padding-5xs: 8px;
  --padding-base-2: 16.2px;
  --padding-mid: 17px;
  --padding-6xs: 7px;
  --padding-12xs: 1px;
  --padding-10xs: 3px;
  --padding-lgi-7: 19.7px;
  --padding-8xs: 5px;
  --padding-xl: 20px;
  --padding-16xl: 35px;
  --padding-11xs-5: 1.5px;
  --padding-7xs: 6px;
  --padding-11xs-8: 1.8px;
  --padding-10xs-2: 2.2px;
  --padding-11xs-7: 1.7px;

  /* border radiuses */
  --br-6xl: 25px;
  --br-3xs: 10px;
  --br-8xs: 5px;
  --br-4xs: 9px;
  --br-11xl: 30px;
  --br-mini: 15px;
}
